import { CameraControls } from "@react-three/drei";
import { OrbitControls, Stats } from '@react-three/drei';
import type { RenderCallback } from '@react-three/fiber';
import { Canvas, useFrame, useThree } from '@react-three/fiber'
import { useMemo, useRef, useState } from "react"
// import DatGui, { DatBoolean, DatColor, DatNumber, DatString } from 'react-dat-gui-x';
import DatGui, { DatBoolean, DatColor, DatNumber, DatString } from 'react-dat-gui';

type DatData = {
    package: string,
    a: number,
    b: number,
    f: number,
    g: number,
    d: number,
}


// extend({ CameraControls })


const numParticles = 1220000;

const AA = () => {
    const points = useRef<typeof points>(null);
    const pointsBuffer = useMemo(() => {
        // Create a Float32Array of count*3 length
        // -> we are going to generate the x, y, and z values for 2000 particles
        // -> thus we need 6000 items in this array
        const positions = new Float32Array(numParticles * 3);

        for (let i = 0; i < numParticles; i++) {
            // Generate random values for x, y, and z on every loop
            const x = (Math.random() - 0.5) * 2;
            const y = (Math.random() - 0.5) * 2;
            const z = (Math.random() - 0.5) * 2;

            // We add the 3 values to the attribute array for every loop
            positions.set([x, y, z], i * 3);
        }

        return positions;
    }, []);

    const { internal } = useThree()
    // on every frame recalculates the position of every particle
    // and updates the attribute with the new values
    const tick: RenderCallback = (time, delta, frame) => {
        // console.log(internal.frames)
        const currentFrame = delta
        // console.log(frame)
        if (time.gl.info.render.frame % 10 !== 0) {
            return
        }
        const positions = pointsBuffer;

        for (let i = 0; i < numParticles; i++) {
            const i3 = i * 3;

            // We will update the y coordinate of every particle
            // by taking the current y value and adding a random value
            // between -0.01 and 0.01
            positions[i3 + 1] = (Math.random() - 0.5) * 2;
        }

        // Update the attribute with the new positions
        if (points.current) {
            points.current.geometry.attributes.position.needsUpdate = true;
        }
    }

    useFrame(tick);

    return (<points ref={points}>
        <bufferGeometry attach="geometry">
            <bufferAttribute
                attach="attributes-position"
                count={pointsBuffer.length / 3}
                array={pointsBuffer}
                itemSize={4}
            />
        </bufferGeometry>

        <pointsMaterial
            size={0.015}
            color="#fff"
            sizeAttenuation
            depthWrite={false}
        />

    </points>)

}



const A = () => {
    const [x, setX] = useState(0)
    const [y, setY] = useState(0)
    const [z, setZ] = useState(0)
    const [datData, setDatData] = useState<DatData>({
        package: 'react-dat-gui',

        a: 1.111,
        b: 4.494,
        f: 1.479,
        g: 0.44,
        d: 0.13
    })

    const canvas = useRef<HTMLCanvasElement>(null)

    return (
        <>
            <div style={{
                position: "fixed",
                top: 0,
                right: 0,
                zIndex: 999
            }} >
                <DatGui data={datData} onUpdate={(data: DatData) => {
                setDatData(data)
                }}

                // style={{ position: "absolute", top: 0, right: 0, zIndex: 999 }}

                >
                    <DatNumber path="a" min={0.5} max={1.5} step={0.0001} key={'a'} />
                <DatNumber path="b" min={4} max={5} step={0.0001} />
                <DatNumber path="g" min={0} max={2} step={0.0001} />
                <DatNumber path="f" min={0} max={1.5} step={0.0001} />
                <DatNumber path="d" min={0.1} max={0.15} step={0.00001} />
            </DatGui>

            </div>
            <Stats showPanel={0} parent={canvas} />

            <Canvas
                ref={canvas}
                // orthographic={true}
                style={{
                    height: "100%",
                    width: "100%",
                    background: "#000"
                }}
                // camera={{
                //     position: [0, 0, 1],
                //     fov: 75,
                //     near: 0.1,
                //     far: 1000,
                // }}


            >

                {/* <Leva oneLineLabels={true} fill={true} /> */}
                <OrbitControls makeDefault />

                {/* <CameraControls camera={<perspectiveCamera />} ref={controls}/> */}
                <ambientLight />
                <pointLight position={[10, 10, 10]} />
                <AA />
                {/* <camera position={[0, 0, 1]} /> */}

            </Canvas>
        </>
    )

}

export default A
